import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Title } from '../entity';
import { EntityService } from '../entity.service';
@Component({
  selector: 'app-update',
  templateUrl: './updateTitle.component.html',
  styleUrls: ['./updateTitle.component.css']
})
export class UpdateTitleComponent implements OnInit {

  @Input() title: Title;

  constructor(
    private route: ActivatedRoute,
    private titleService: EntityService,
    private location: Location
  ) {
    this.route.params.subscribe(data => console.log(data.mid));
  }

  ngOnInit(): void {
    const id = this.route.snapshot.params.id;
    this.titleService.getTitle (id)
      .subscribe(res => this.title = res);
    /****
     this.route.queryParams
     .subscribe((params: College) => {
        this.college = params;
      });
     ******/

  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.titleService.updateTitle(this.title)
      .subscribe(() => this.goBack());
  }
}
