import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {EntityService} from '../entity.service';
import { School } from '../entity';

@Component({
  selector: 'app-add-school',
  templateUrl: './addSchool.component.html',
  styleUrls: ['./addSchool.component.css']
})
export class AddSchoolComponent implements OnInit {
  @Input() school: School;
  constructor(
    private route: ActivatedRoute,
    private schoolService: EntityService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  save(description: string, no: string, remarks: string): void { //多个文本框数据
    this.school = {id: null, no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.schoolService.addSchool(this.school)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}

