import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-add-title',
  templateUrl: './addTitle.component.html',
  styleUrls: ['./addTitle.component.css']
})
export class AddTitleComponent implements OnInit {
  @Input() title: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private titleService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {}

  save( no: string, description: string, remarks: string): void { //多个文本框数据
    this.title = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.titleService.addTitle(this.title)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
