import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { EntityService} from '../entity.service';
import { Teacher, Degree, Title, School,  Department , Teacher1} from '../entity';

@Component({
  selector: 'app-add-teacher',
  templateUrl: './addTeacher.component.html',
})
export class AddTeacherComponent implements OnInit {
  @Input() schools: School[];
  degrees: Degree[];
  titles: Title[];
  departments: Department[];
  teacher: Teacher1;
  school: School;
  degree: Degree;
  title: Title;
  department: Department;
  constructor(
    private route: ActivatedRoute,
    private teacherService: EntityService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getSchools() ;
    this.getDegrees();
    this.getTitles();
    this.getDepartments();
  }
  getSchools(): void {
    this.teacherService.getSchools ()
      .subscribe(res => this.schools = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  getTitles(): void {
    this.teacherService.getTitles ()
      .subscribe(res => this.titles = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  getDepSch(id: number): void {
    this.teacherService.getDepSch (id)
      .subscribe(res => this.departments = res);
  }
  save(no: string, name: string, title: Title, degree: Degree, department: Department): void { //多个文本框数据
    this.teacher = {no: no.trim(), name: name.trim(), title: this.title, degree: this.degree, department: this.department};
    if (!name) { return; }
    this.teacherService.addTeacher(this.teacher)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
