import { Component, OnInit } from '@angular/core';
import {Department, School} from '../entity';
import { EntityService} from '../entity.service';

@Component({
  selector: 'app-school',
  templateUrl: './school.component.html',
  styleUrls: ['./school.component.css']
})
export class SchoolComponent implements OnInit {
  private n: number;
  private dataShow: School[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;
  schools: School[];
  constructor(private schoolService: EntityService) { }
  /*发生于构造函数之后，用于初始化指令/组件，主要用于数据绑定的输入属性处理*/
  ngOnInit() {
    this.getSchools();
  }
  // DoCheck 触发变更检测机制就是调用DoCheck钩子，目的是保证组件属性和浏览器的显示同步
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.schools[startNum] !== null) {
          this.dataShow.push(this.schools[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getSchools(): void {
    this.schoolService.getSchools ()
      .subscribe(res => this.schools = res);
  }
  delete(school: School | number): void {
    this.schools = this.schools.filter(h => h !== school);
    this.schoolService.deleteSchool(school).subscribe();
  }
}
