import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SchoolComponent } from './school/school.component';
import { TeacherComponent } from './teacher/teacher.component';
import { TitleComponent } from './title/title.component';
import { DegreeComponent } from './degree/degree.component';
import { DepartmentComponent } from './department/department.component';
import { DetaillSchoolComponent } from './school/detaillSchool.component';
import { AddSchoolComponent } from './school/addSchool.component';
import { UpdateSchoolComponent } from './school/updateSchool.component';
import { DetaillDegreeComponent } from './degree/detaillDegree.component';
import { AddDegreeComponent } from './degree/addDegree.component';
import { UpdateDegreeComponent } from './degree/updateDegree.component';
import { DetaillDepartmentComponent } from './department/detaillDepartment.component';
import { AddDepartmentComponent } from './department/addDepartment.component';
import { UpdateDepartmentComponent } from './department/updateDepartment.component';
import { DetaillTitleComponent } from './title/detaillTitle.component';
import { AddTitleComponent } from './title/addTitle.component';
import { UpdateTitleComponent } from './title/updateTitle.component';
import { DetaillTeacherComponent } from './teacher/detaillTeacher.component';
import { AddTeacherComponent } from './teacher/addTeacher.component';
import { UpdateTeacherComponent } from './teacher/updateTeacher.component';
import { LoginComponent } from './login/login.component';
import { IndexComponent } from './index/index.component';
import { PageComponent } from './page/page.component';
const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'}, // 默认路由(登录)
  {path: 'index', component: IndexComponent, children: [ // 嵌套路由
  { path: '', redirectTo: '/school', pathMatch: 'full' }, // 空路径 重定向
  { path: 'school', component: SchoolComponent},
  { path: 'teacher', component: TeacherComponent},
  { path: 'title', component: TitleComponent},
  { path: 'degree', component: DegreeComponent},
  { path: 'department', component: DepartmentComponent},
  { path: 'addSchool', component: AddSchoolComponent},
  { path: 'updateSchool/:id', component: UpdateSchoolComponent},
  { path: 'detailSchool/:id', component: DetaillSchoolComponent},
  { path: 'addDegree', component: AddDegreeComponent},
  { path: 'updateDegree/:id', component: UpdateDegreeComponent},
  { path: 'detailDegree/:id', component: DetaillDegreeComponent},
  { path: 'addDepartment', component: AddDepartmentComponent},
  { path: 'updateDepartment/:id', component: UpdateDepartmentComponent},
  { path: 'detailDepartment/:id', component: DetaillDepartmentComponent},
  { path: 'addTitle', component: AddTitleComponent},
  { path: 'updateTitle/:id', component: UpdateTitleComponent},
  { path: 'detailTitle/:id', component: DetaillTitleComponent},
  { path: 'addTeacher', component: AddTeacherComponent},
  { path: 'updateTeacher/:id', component: UpdateTeacherComponent},
  { path: 'detailTeacher/:id', component: DetaillTeacherComponent},
  { path: 'login', component: LoginComponent},
  { path: 'index', component: IndexComponent},
  { path: 'page', component: PageComponent},
    ]},
];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
