import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Title } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-detaill',
  templateUrl: './detaillTitle.component.html',
  styleUrls: ['./detaillTitle.component.css']
})
export class DetaillTitleComponent implements OnInit {

  @Input() title: Title;

  constructor(
    private route: ActivatedRoute,
    private titleService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: Title) => {
               this.title = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
